'use client';

import React from 'react';
import {
  Box,
  Avatar,
  Typography,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Collapse,
  IconButton,
  Drawer,
  Toolbar,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  Dashboard as DashboardIcon,
  PeopleAlt as PeopleAltIcon,
  Settings as SettingsIcon,
  Logout as LogoutIcon,
  ExpandLess,
  ExpandMore,
  Person,
  Password,
  AccountCircle,
  Menu as MenuIcon,
} from '@mui/icons-material';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@/contexts/UserContext';

const drawerWidth = 240;

const Sidebar = () => {
  const { user } = useUser();
  const router = useRouter();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [openDrawer, setOpenDrawer] = useState(!isMobile);
  const [openSettings, setOpenSettings] = useState(false);

  const handleToggleDrawer = () => {
    setOpenDrawer(!openDrawer);
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    router.push('/login');
  };

  const handleMenuClick = (path: string) => {
    router.push(path);
  };

  const drawerContent = (
    <Box
      sx={{
        width: drawerWidth,
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100vh',
        bgcolor: '#001F3F',
        color: '#fff',
        paddingY: 2,
        px: 1.5,
      }}
    >
      <Toolbar />
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          textAlign: 'center',
          mb: 2,
        }}
      >
        <Avatar
          src={user?.profilePicture || ''}
          alt={user?.fullName || 'User'}
          sx={{ width: 70, height: 70, mb: 1, border: '2px solid white' }}
        />
        <Typography variant="h6">{user?.fullName || 'Loading...'}</Typography>
        <Typography variant="body2" sx={{ color: '#BBB' }}>
          {user?.email}
        </Typography>
      </Box>

      <Divider sx={{ bgcolor: '#ccc', my: 2 }} />

      <List>
        <ListItem button onClick={() => handleMenuClick('/dashboard')}>
          <ListItemIcon>
            <DashboardIcon sx={{ color: '#7FDBFF' }} />
          </ListItemIcon>
          <ListItemText primary="Dashboard" />
        </ListItem>

        <ListItem button onClick={() => handleMenuClick('/employees')}>
          <ListItemIcon>
            <PeopleAltIcon sx={{ color: '#7FDBFF' }} />
          </ListItemIcon>
          <ListItemText primary="Employees" />
        </ListItem>

        <ListItem button onClick={() => setOpenSettings(!openSettings)}>
          <ListItemIcon>
            <SettingsIcon sx={{ color: '#7FDBFF' }} />
          </ListItemIcon>
          <ListItemText primary="Settings" />
          {openSettings ? <ExpandLess /> : <ExpandMore />}
        </ListItem>

        <Collapse in={openSettings} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem
              button
              sx={{ pl: 4 }}
              onClick={() => handleMenuClick('/profile')}
            >
              <ListItemIcon>
                <AccountCircle sx={{ color: '#39CCCC' }} />
              </ListItemIcon>
              <ListItemText primary="View Profile" />
            </ListItem>

            <ListItem
              button
              sx={{ pl: 4 }}
              onClick={() => handleMenuClick('/change-password')}
            >
              <ListItemIcon>
                <Password sx={{ color: '#39CCCC' }} />
              </ListItemIcon>
              <ListItemText primary="Change Password" />
            </ListItem>
          </List>
        </Collapse>

        <Divider sx={{ my: 2, bgcolor: '#ccc' }} />

        <ListItem button onClick={handleLogout}>
          <ListItemIcon>
            <LogoutIcon sx={{ color: '#FF4136' }} />
          </ListItemIcon>
          <ListItemText primary="Logout" />
        </ListItem>
      </List>

      <Box flexGrow={1} />

      <Box textAlign="center" py={2}>
        <Typography variant="caption" sx={{ color: '#888' }}>
          Remote Monitoring System (RMS-IPIS)
        </Typography>
      </Box>
    </Box>
  );

  return (
    <>
      {isMobile && (
        <IconButton
          onClick={handleToggleDrawer}
          sx={{
            position: 'fixed',
            top: 10,
            left: 10,
            zIndex: 1201,
            bgcolor: '#0074D9',
            color: '#fff',
            '&:hover': {
              bgcolor: '#005fa3',
            },
          }}
        >
          <MenuIcon />
        </IconButton>
      )}

      <Drawer
        variant={isMobile ? 'temporary' : 'persistent'}
        open={openDrawer}
        onClose={handleToggleDrawer}
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
            transition: 'all 0.3s',
          },
        }}
      >
        {drawerContent}
      </Drawer>
    </>
  );
};

export default Sidebar;
